@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



form {
  /* margin: 5rem auto 0; */
  width: 24rem;
  color: #fff;
  border-color: #8bc34a;
}
body{font-family:sans-serif;background:#1a1a1a}form{margin:1rem auto 0;width:24rem;color:#fff}form fieldset{margin-bottom:2rem;border-radius:4px;border-color:#8bc34a}form input{margin-right:0.5rem}form input:not(:first-of-type){align-items:center}form input[type="submit"]{width:5rem;height:2.5rem;border:none;border-radius:2px;font-size:1rem;color:#fff;background:#8bc34a;cursor:pointer}form .container{display:flex;align-items:center;justify-content:center;box-shadow:0 4px 8px 0 rgba(0,0,0,0.2);transition:0.3s;border-radius:5px;align-content:center;text-align:center;width:500px;padding:5px;margin:5px}

.container {
  height: 100%;
  width: 40%;
  padding-top: 1rem;
  margin-left: 30%;
  /* display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex; */
  /* box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  transition: 0.3s; */
  display: flex;
  justify-content: center;  
}

.flex-item {
  list-style-type: none;
  text-align: center;
}
*{margin:0;padding:0}.canvas{height:100vh;background-size:cover;background-position:center}.location-search-input{font-family:'Lato', sans-serif;width:18rem;height:40px;background:none;border:2px solid #8bc34a;border-radius:10px;font-size:1rem;color:black;font-weight:bold;margin-bottom:1rem;margin-top:100px}.input-suggestion{font-family:'Lato', sans-serif;width:500px;height:60px;background-color:white;border-bottom:1px black dotted;display:flex;align-items:center;font-size:1rem;font-weight:bold;color:black}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.button {
  cursor: pointer;
  display: block;
  font-size: 1.3em;
  box-sizing: content-box;
  margin: 20px auto 0px;
  width: 70%;
  padding: 15px 20px;
  border-radius: 24px;
  border-color: transparent;
  background-color: white;
  /* box-shadow: 0px 16px 60px rgba(78, 79, 114, 0.1); */
  box-shadow: 0px 16px 60px rgba(78, 79, 114, 0.08);
  position: relative;
}

.buttonText {
  color: #4285f4;
  font-weight: bolder;
}

.icon {
  height: 25px;
  width: 25px;
  margin-right: 0px;
  position: absolute;
  left: 30px;
  align-items: center;
}
